.bannerContainer {
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 50px;
  margin-bottom: 50px;
}

.introBanner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 25px;
  margin-right: 25px;
}

.headBanner {
  font-weight: bolder;
  font-size: 160px;
  margin-top: 100px;
  color: black;
}

.bodyBanner {
  font-weight: bold;
  font-size: 20px;
  margin-bottom: 100px;
  color: black;
}

.fractionBanner {
  display: flex;
  align-items: center;
  border-radius: 20px;
}

.fraction {
  max-height: 500px;
  border-radius: 20px;
}

.discordButton {
  font-size: 16px;
  cursor: pointer;
  font-family: "CaviarDreams";
  border: 2px solid black;
  background-color: #ffffff;
  color: black;
  font-weight: bold;
  padding: 18px;
  justify-content: center;
  width: 120px;
  margin-left: 15px;
}

.openseaButton {
  font-size: 16px;
  cursor: pointer;
  font-family: "CaviarDreams";
  border: 2px solid black;
  font-weight: bold;
  padding: 18px;
  justify-content: center;
  width: 120px;
  margin-right: 15px;
}

.mintBanner {
  display: flex;
  justify-content: center;
}

.mintButton {
  font-size: 20px;
  cursor: pointer;
  font-family: "CaviarDreams";
  border: 2px solid black;
  font-weight: bold;
  padding: 18px;
  justify-content: center;
  width: 120px;
  margin-right: 15px;
  margin-top: 15px;
}

.joinBanner {
  display: flex;
  justify-content: space-around;
}

@media only screen and (max-width: 1000px) {
  .bannerContainer {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 10px;
  }

  .introBanner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 20px;
  }

  .headBanner {
    font-weight: bolder;
    font-size: 120px;
    margin-top: 20px;
    color: black;
  }

  .bodyBanner {
    font-weight: bold;
    font-size: 16px;
    margin-bottom: 30px;
    color: black;
  }

  .fractionBanner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .fraction {
    max-height: 300px;
    border-radius: 20px;
  }

  .discordButton {
    font-size: 14px;
    cursor: pointer;
    font-family: "CaviarDreams";
    border: 2px solid black;
    background-color: #ffffff;
    color: black;
    font-weight: bold;
    justify-content: center;
    width: 120px;
    margin-left: 0px;
    margin-bottom: 10px;
  }

  .openseaButton {
    font-size: 14px;
    cursor: pointer;
    font-family: "CaviarDreams";
    border: 2px solid black;
    font-weight: bold;
    justify-content: center;
    width: 120px;
    margin-right: 0px;
  }

  .joinBanner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
