.utilityContainer {
  align-items: center;
  padding-left: 300px;
  padding-right: 300px;
}

.utilityBanner {
  height: fit-content;
  display: flex;
  flex-direction: column-reverse;
  align-content: center;
}

.bannerImage {
  object-fit: contain;
  align-items: center;
}

.utilityHeader {
  font-size: 30px;
  font-weight: bold;
  color: black;
  padding-top: 50px;
  margin-bottom: 10px;
}

.utilityBody {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  color: black;
  margin-left: 100px;
  margin-right: 100px;
  margin-top: 100px;
}

.textHeader {
  font-size: 48px;
  font-weight: bolder;
  color: black;
  text-align: left;
}

.textBody {
  font-size: 18px;
  font-weight: normal;
  margin-top: 10px;
  margin-right: 25px;
  margin-bottom: 30px;
  text-align: left;
}

.imagebox {
  max-height: 450px;
}

.Definition {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.treasuryFund {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

.tokenAirdrop {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

.Community {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 100px;
}

@media only screen and (max-width: 500px) {
  .utilityContainer {
    background-image: url("../assets/header/Braq_Web_Banner-03.png");
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
  }

  .utilityHeader {
    font-size: 30px;
    font-weight: bold;
    color: black;
    padding-top: 50px;
    margin-bottom: 10px;
  }

  .utilityBody {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: black;
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 30px;
  }

  .utilityContainer {
    align-items: center;
    margin-left: 5px;
    margin-right: 5px;
    padding-left: 5px;
    padding-right: 5px;
  }

  .Definition {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .treasuryFund {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }

  .tokenAirdrop {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 50px;
  }

  .Community {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 50px;
  }

  .textHeader {
    font-size: 32px;
    font-weight: bolder;
    color: black;
    text-align: left;
  }

  .textBody {
    font-size: 12px;
    font-weight: normal;
    margin-top: 10px;
    margin-right: 25px;
    margin-bottom: 30px;
    text-align: left;
  }

  .imagebox {
    max-height: 200px;
  }
}

