.faqContainer {
  background-color: white;
}

.faqContent {
  display: flex;
  font-size: 40px;
  color: black;
  font-weight: bold;
  justify-content: space-around;
  align-items: center;
}

.faq {
  display: flex;
  justify-content: center;
  margin-top: 100px;
}

.FAQheader {
  font-size: 40px;
  font-weight: bold;
  color: black;
}

@media only screen and (max-width: 500px) {
  .faqText {
    display: flex;
    font-size: 18px;
    font-weight: bold;
    font-family: "CaviarDreams";
    color: black;
    justify-content: center;
    align-items: flex-end;
  }
}

@media only screen and (max-width: 450px) {
  .faq {
    display: flex;
    justify-content: center;
    margin-top: 100px;
  }
}

@media only screen and (max-width: 600px) {
  .FAQheader {
    font-size: 26px;
  }
}
