.collections {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 100px;
}

.ourCollections {
  font-size: 48px;
  font-weight: bold;
}

.collectionImages {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 620px;
}

.braqFrnds {
  display: flex;
  flex-direction: row;
  margin-bottom: 100px;
}

.braqMonsters {
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 100px;
}

.braqFA {
  display: flex;
  flex-direction: row;
  margin-bottom: 100px;
}

.collectionHeader {
  font-size: 40px;
  font-weight: bold;
}

.collectionBody {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  justify-content: left;
}

.imageStack {
  max-width: 200px;
  border-radius: 10px;
  margin: 2px;
}

.collectionTextContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 50px;
}

.collectionText {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 500px;
}

.collectionJoin {
  display: flex;
}

.buyOpensea {
  font-size: 18px;
  font-weight: bold;
  border: 3px solid black;
  padding: 25px;
  margin-top: 50px;
  border-radius: 20px;
  cursor: pointer;
}

.faImage {
  max-height: 600px;
  border-radius: 20px;
  margin: 10px;
}

