.footerContainer {
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  padding-top: 20px;
  align-items: center;
  margin-bottom: 40px;
}

.footerLogo {
  max-width: 150px;
}

.trademark {
  font-size: 18px;
  font-family: "CaviarDreams";
  font-weight: bolder;
  color: black;
}

.termsContainer {
  display: flex;
}

.terms {
  margin-left: 25px;
  margin-right: 25px;
  cursor: pointer;
  color: black;
}

@media only screen and (max-width: 600px) {
  .footerContainer {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    padding-top: 20px;
    align-items: center;
    margin-bottom: 40px;
  }

  .footerLogo {
    max-width: 50px;
  }

  .trademark {
    font-size: 14px;
    font-family: "CaviarDreams";
    font-weight: bolder;
    color: black;
  }

  .termsContainer {
    display: flex;
  }

  .terms {
    margin-left: 25px;
    margin-right: 25px;
    cursor: pointer;
    color: black;
    font-size: 10px;
  }
}
