.headerContainer {
  display: flex;
  background-color: white;
  justify-content: space-evenly;
}

.emptyContainer {
  width: 33%;
}

.headerlogoContainer {
  width: 33%;
}

.headerLogo {
  max-width: 20%;
}

.socialContainer {
  width: 33%;
  justify-content: space-evenly;
  display: flex;
}

.downloadWhitepaper {
  max-height: 50px;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  cursor: pointer;
  border: 2px solid #000000;
  padding: 18px;
  font-weight: bold;
  color: black;
  border-radius: 20px;
  margin-right: 20px;
}

.connectWallet {
  max-height: 50px;
  cursor: pointer;
  font-size: 16px;
  display: flex;
  cursor: pointer;
  border: 2px solid #000000;
  padding: 18px;
  font-weight: bold;
  color: black;
  border-radius: 20px;
}

.headerButton {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .headerContainer {
    display: flex;
    background-color: white;
    margin-top: 20px;
    margin-left: 25px;
    margin-right: 25px;
    justify-content: space-evenly;
  }

  .downloadWhitepaper {
    max-height: 25px;
    cursor: pointer;
    font-size: 10px;
    display: flex;
    cursor: pointer;
    border: 2px solid #000000;
    padding: 10px;
    font-weight: bold;
    color: black;
    font-size: small;
    border-radius: 20px;
  }

  .connectWallet {
    max-height: 25px;
    cursor: pointer;
    font-size: 10px;
    display: flex;
    cursor: pointer;
    border: 2px solid #000000;
    padding: 10px;
    font-weight: bold;
    color: black;
    font-size: small;
    border-radius: 20px;
  }

  .socialContainer {
    width: 33%;
    justify-content: space-evenly;
    display: flex;
  }
}
