.aboutContainer {
  width: 100%;
  display: contents;
  background-color: black;
  padding: 50px;
  font-family: "CaviarDreamsBold";
}

.abouttext {
  display: block;
  padding: 20px;
  color: slategrey;
  font-size: 26px;
  text-align: justify;
  font-family: "CaviarDreams";
}

.whitepaper {
  text-decoration: aliceblue;
}

.whitepaperImage {
  max-width: 300px;
  cursor: pointer;
}

.FAQBanner {
  height: fit-content;
  display: flex;
  flex-direction: column-reverse;
  align-content: center;
  background-color: white;
}

.faqBannerImage {
  object-fit: contain;
  align-items: center;
  background-color: white;
}

.teamProfileHeader {
  margin-top: 50px;
  padding: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
  font-weight: bold;
  font-size: 40px;
}

.headcontainer {
  border-radius: 20px;
  padding: 10px;
  width: fit-content;
  color: black;
  font-size: 40px;
  font-family: "CaviarDreams";
}

.teamHeader {
  max-width: 400px;
}
.teamContainer {
  width: 100%;
}

.team {
  font-size: 60px;
}

.profileContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: "CaviarDreams";
  margin-left: 100px;
  margin-right: 100px;
}

.profile {
  margin: 30px;
  padding-bottom: 10px;
  border-color: #f2d5e7;
  height: max-content;
  color: black;
  font-family: "CaviarDreams";
}

.profilePicture > img {
  border-radius: 200px;
  max-width: 250px;
}

.profileDescription {
  display: flex;
  font-family: "CaviarDreams";
  flex-direction: column;
  align-items: center;
}

.nametext {
  font-size: 18px;
  font-weight: bold;
}

.roletext {
  font-size: 14px;
  font-weight: bold;
  color: #f2d5e7;
}

.socialLinks {
  display: flex;
  justify-content: center;
  cursor: pointer;
  font-family: "CaviarDreams";
}

.logo {
  max-width: 25px;
  padding: 5px;
}

